body {
  height: 100vh;
  margin: 0;
}

.container {
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__minutes {
  z-index: 1;
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
  position: absolute;
  bottom: 50%;
}

.stopwatch__seconds {
  z-index: 2;
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
  position: absolute;
  bottom: 50%;
}

.stopwatch__center {
  z-index: 3;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch--speed-up .stopwatch__minutes {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__seconds {
  animation-duration: 10s;
}

@keyframes rotate {
  from {
    rotate: none;
  }

  to {
    rotate: 360deg;
  }
}
